body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo-wrapper {
    display: none;
}

.logo-title {
    font-family: Righteous;
    font-size: 24px;
    line-height: 30px;
}

.checkbox-toggle{
    display: flex;
    align-items: center;
    margin: 0 auto;
    min-height: 36px;
}

.not-eat,
.eat {
    font-family: Roboto;
    font-size: 20px;
    line-height: 23px;
    letter-spacing: 0.01px;
    color: #4D4D4D;
    cursor: pointer;
    margin: 0;
}
input[type="checkbox"] {
    width: 49px;
    height: 3px;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: #c6c6c6;
    outline: none;
    box-shadow: inset 0 0 5px rgba(0,0,0, .2);
    -webkit-transition: 0.5s;
    transition: 0.5s;
    position: relative;
    cursor: pointer;
    margin: 0 14px;
}

input[type="checkbox"]::before {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 3px solid #807E7F;
    top: -9px;
    left: 0;
    background: #fff;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

input:checked[type="checkbox"]::before {
    left: 30px;
    background: #02a9f4;
    border-color: #02a9f4;
}

@media (min-width: 768px) {
    .logo-wrapper {
        position: absolute;
        display: flex;
        flex-direction: row;
        align-items: center;
    }
}
.blockTitle {
    margin: 0;
    font-size: 24px;
}
.App {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  min-width: 320px;
  overflow-x: hidden;
  overflow-y: auto;
  font-family: Roboto;
}
.App-logo {
  height: 36px;
  width: 36px;
  margin-right: 10px;
}

.App-header {
  position: fixed;
  width: 100%;
  top: 0;
  background-color: #fff;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.26);
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px;
}

.pageWrapper {
  margin-top: 60px;
}
